<template>
  <div class="style-guide">
    <div class="aligner-item">
      <h1>Component</h1>
      <div class="dao-view-main">
        <div class="dao-view-sidebar">
          <div class="dao-list-group-container">
            <ul class="dao-list-group"
                v-for="(group, index) in SIDE_BAR"
                :key="index">
              <li class="dao-list-item"
                  v-for="tab in group"
                  :key="tab.id"
                  :class="{ 'active' : step === tab.id }"
                  @click="switchStep(tab.id)">
                <div>
                  {{ tab.name }}
                  <span class="icon">
                <svg><use xlink:href="#icon_caret-right"></use></svg>
              </span>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <div class="dao-view-content">
          <list-panel
            v-if="step === STEP.LIST"
          />
          <table-panel
            v-if="step === STEP.TABLE"
          />
        </div>
      </div>
    </div>
    <div class="aligner-item">
      <h1>Color</h1>

      <div class="color-block">
        <div v-for="(item, key, index) in colorObject">
          <div  class="color-container">
            <h2>{{ key }} : {{ item }} </h2>
            <div class="color-card" v-bind:style="{ 'background-color': item }"></div>
          </div>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import ListPanel from './ListPanel';
import TablePanel from './TablePanel';

const STEP = {
  LIST: 'list',
  TABLE: 'table',
};

export default {
  name: 'hello',
  components: {
    ListPanel,
    TablePanel,
  },
  data() {
    return {
      step: STEP.LIST,
      STEP,
      SIDE_BAR: [
        [
          { name: 'LIST', id: STEP.LIST },
          { name: 'TABLE', id: STEP.TABLE },
        ],
      ],
      list: [
        { name: '春雨 (Spring Rain)', albumName: '봄비', singer: 'BoA' },
        { name: 'Be As One', albumName: 'Be As One/Let\'s get it...', singer: 'w-winds.' },
        { name: 'Places', albumName: 'Places', singer: 'Osrin' },
      ],
      colorObject: {
        blue: '#3890ff',
        green: '#22c36a',
        yellow: '#f7b32b',
        orange: '#f56e25',
        red: '#f1483f',
        purple: '#7354e2',
        'black-dark': '#3d444f',
        'black-darker': '#1f2126',
        'black-light': '#595f69',
        'grey-dark': '#9ba3af',
        'grey-light': '#ccd1d9',
        'white-dark': '#e4e7ed',
        'white-dark-lighter': '#f1f3f6',
        'white-light': '#f5f7fa',
        'white-lighter': '#fbfcfc',
        white: '#ffffff',
      },
    };
  },
  methods: {
    switchStep(step) {
      this.step = step;
    },
  },
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
  @import '../style/index';
  .style-guide {
    h1 {
      margin: 40px 0;
    }
    .aligner-item {
      margin: $dao-view-margin-v $dao-view-margin-h 0 $dao-view-margin-h;
      padding: 0 0 $dao-view-margin-h 0;
      border-bottom: 1px solid $grey-light;
    }
    .color-block {
      display: flex;
      align-content: center;
      align-items: center;;
      flex-wrap: wrap;
    }
    .color-container {
      width: 300px;
      height: 100px;
      margin: $dao-view-margin-v 0 $dao-view-margin-h;
    }
    .color-card {
      width: 80px;
      height: 80px;
      border: 1px solid $black-dark;
    }
    .blue {
      color: $blue;
    }
  }
</style>



