// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import DaoStyle from 'dao-style-vue';
// still inject dao-style-vue style
import 'dao-style-vue/styles/dao-style.css'; // eslint-disable-line

import App from './App';

// inject style(laytou, custom style)
import './style/index.scss'; // eslint-disable-line

Vue.config.productionTip = false;

// inject dao-style-vue as vue plugin
Vue.use(DaoStyle);

/* eslint-disable no-new */
new Vue({
  el: '#app',
  template: '<App/>',
  components: { App },
});



