<template>
  <div class="dao-setting-layout">
    <div class="dao-setting-layout-title">
      <div class="dao-setting-title">
        TABLE
      </div>
    </div>
    <div class="dao-setting-section">
      <div class="dao-table-container">
        <div class="dao-table-main">
          <table class="dao-table row">
            <thead>
              <tr>
                <th>键</th>
                <th>值</th>
              </tr>
            </thead>
            <tr v-for="[key, value] in parsedEnvs" :key="key">
              <td>{{ key }}</td>
              <td>{{ value }}</td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'TablePanel',
  data() {
    return {
      envs: [
        'id1=123',
        'id2=234',
        'id3=345',
      ],
    };
  },
  computed: {
    parsedEnvs() {
      return this.envs.map(x => x.split('='));
    },
  },
};
</script>



